$orange: #FF8469;
$orange--dark: #D95133;
$blue: #4F5565;
$blue--dark: #414B62;
$white: #FFFFFF;

*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body{
  font-family: 'Montserrat', sans-serif;
}

.page{
  padding: 100px 15px 235px 16px;
  background: radial-gradient(25% 25% at 25% 25%, #252E42 0%, #161C2E 100%);
  background-size: 200% 200%;
  animation:lazyBackgroundAnimate 15s linear infinite;
}

@media screen and (min-width: 1440px){
  .page{
    padding-top: 120px;
    padding-bottom: 272px;
  }
}

.frame {
  background: #252E42;
  box-shadow: 0 14px 34px rgba(0, 0, 0, 0.25);
  border-radius: 20px 0 40px;
  height: 540px;
  position: relative;
  z-index: 1;
}
.frame-container{
  position: relative;
  max-width: 420px;
  margin: 0 auto;
  &:after{
    content: '';
    position: absolute;
    width: 328.11px;
    height: 67.95px;
    background: linear-gradient(180deg, #31394D 0%, #091739 100%);
    border-radius: 20px;
    transform: matrix(1, -0.05, 0.03, 1, 0, 0);
    top: -25px;
    left: 0;
  }
  &:before{
    content: '';
    position: absolute;
    width: 284.18px;
    height: 85.15px;
    background: linear-gradient(180deg, #4F5565 0%, #000000 53.65%);
    border-radius: 20px;
    transform: matrix(1, -0.09, 0.06, 1, 0, 0);
    left: 0;
    top: -41px;
  }
}

@media screen and (min-width: 1440px){
  .frame-container {
    &:before {
      width: 341.86px;
      height: 85.23px;
    }
    &:after {
      width: 395.1px;
      height: 67.97px;
    }
  }
}

.headline{
  display: flex;
  justify-content: space-between;
  align-items: center;

  margin-bottom: 20px;
  padding: 30px;
  padding-bottom: 0;
}

.h1{
  font-weight: 700;
  font-size: 28px;
  line-height: 34px;
  color: #FFFFFF;
}

.button{
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  text-transform: uppercase;
  color: #FFFFFF;
  padding: 5px 10px;
  background-color: $orange;
  border-radius: 7px;
  border: 0;
  outline: none;
  transition: 100ms;
  cursor: pointer;
  &:hover{
    background-color: $orange--dark;
  }
}

.content {
  position: relative;
  padding-left: 30px;
  padding-right: 30px;
  &--scroll {
    padding-right: 16px;
    &:after {
      content: '';
      width: calc(100% - 30px);
      height: 75px;
      background: linear-gradient(180deg, rgba(37, 46, 66, 0) 0%, #252E42 67.19%);
      position: absolute;
      bottom: 0;
      left: 0;
    }
  }
}

.list{
  padding-right: 14px;
}
// придумать, как высчитать высоту для блока чтобы был норм скролл
.list--scroll{
  overflow-y: scroll;
  height: 300px;
  &::-webkit-scrollbar {
    width: 4px;
    background-color: transparent;
  }
  &::-webkit-scrollbar-thumb {
    background-color: $orange;
    border-radius: 4px;
  }

}
.list__item{
  margin-bottom: 18px;
    &:last-child{
      margin-bottom: 0;
    }
}

.item{
  display: flex;
  align-items: center;
  width: 100%;
}

.checkbox-container--left{
  margin-right: 15px;
}

.checkbox-hide{
  display: none;
}

.checkbox{
  border-radius: 6px;
  width: 20px;
  height: 20px;
  display: block;
  transition: 100ms;
  cursor: pointer;
  background: $blue no-repeat center / 12px;
  .checkbox-hide:checked ~ &{
    background-color: $orange;
    background-image: url("../img/ic_check.svg");
  }
}

.text-container{
  cursor: pointer;
  overflow: hidden;
  width: 100%;
}

.title{
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: $white;
  transition: 100ms;
  &:hover{
    color: $orange;
  }
  &--done{
    text-decoration-line: line-through;
    color: $blue;
    &:hover{
      color: $white;
    }
  }
}

.description{
  font-weight: 400;
  font-size: 12px;
  line-height: 15px;
  color: $blue;
  position: relative;
  &--nowrap{
    white-space: nowrap;
  }
  &--nowrap:after{
    content: '';
    width: 34px;
    height: 12px;
    background: linear-gradient(-90deg, #252E42 9.38%, rgba(37, 46, 66, 0) 66.15%);
    position: absolute;
    right: 0;
    top: 1px;
  }
  &--done{
    text-decoration-line: line-through;
  }
  &--margin-top{
    margin-top: 3px;
  }
}

.copyright{
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  color: $blue--dark;
  &--center{
    text-align: center;
  }
  &--margin-top{
    margin-top: 30px;
  }
}

@keyframes lazyBackgroundAnimate {
  0%,100%{
    background-position: -20% 0%;
  }
  25%{
    background-position: -20% -20%;
  }
  50%{
    background-position: 20% -20%;
  }
  75%{
    background-position: -20% 20%;
  }
}